@import "News.css";
$color:white;
$primary-color:#f9c803;
$font:'Roboto Condensed',sans-serif;

body {
  background-color:$color;
}

html{
  font-size: 10px;
}
.Header{
  width: 100%;
  background-size: cover;
  background-repeat:no-repeat;
  margin: auto;
  margin-top:0;
  position: relative;
  text-align: center;

}
.main_logo{
  border-style: none;
  height: 46px;
  vertical-align: inherit;
  z-index: 1;

}

.sub_logo{
  font-family: "Arial Narrow";
  font-size:15px;
  color:#5bc0de;

  z-index: 1;
}

.navbar-toggle{

  clear: both;
  margin-top: 45px;


}
#Listitem a::after {
    position: absolute;
    top: -2px;
    right: 100%;
    content: '';
    border-width: 46px;
    border-style: solid;
    border-color: transparent #ffcc00 #ffcc00 transparent;
  }

nav{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  background-color:inherit;

}
.navbar {
  width: 100%;
  top: 0;
  background-color: $color;
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  li {
    display: inline-block;
    }
  a {
    color:$color;
    display: inline-table !important;
    text-decoration: none;
    &:hover{
      background-color:transparent!important;
      text-decoration: none;
    }
    &:focus{
      background-color:transparent !important;
    }
  }
}

/*active navbar with border bottom a*/
.nav-item>a.navbar-link.active::after{
  content: "";
  display: block;
  height: 2px;
  background-color: #fff;
  width: 20px;
  position: absolute;
  top: 0;
  bottom: -20px;
  left: 0;
  right: 0;
  margin: auto;
}
.navbar-brand{
  position: absolute;
  top: 7px;
  left: 30px;
}
/*responsive navbar for ipad*/
@media screen and (min-width: 768px) {
  .navbar ul li a {
    height: 90px;
    line-height: 50px;
    margin-right: 10px;
    font-size: 9px;
  }
  .logo {
    img {
    width: 70px;
    height: 40px;
   }
 p{
    font-size: 10px;
    margin-top: 5px;
    color: #0099ff;
  }
}
}
/*reponsive navbar for ipad pro and computer*/
@media screen and (min-width: 1024px) {
  .navbar ul li a {
    height: 90px;
    line-height: 50px;
    margin-right: 20px;
    font-size: 13px;
  }
  .logo img{
    width: 110px;
    height: 50px;
  }
  .logo p{
    font-size: 12px;
    margin-top: 5px;
    color: #0099ff;
  }
}

/*reponsive  navbar for type phone */
@media screen and (max-width: 415px) {
  .navbar ul li a {
    height: 30px;
    line-height: 15px;
    /*margin-right: 30px;*/
    font-size: 12px;
  }
  .logo {
    img {
      width: 90px;
      height: 30px;
    }
    p {
      margin-top: 0;
      font-size: 7px;
      color: #0099ff;
    }
  }
}
